div.tasks{
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #999;
}
div.text{
    padding: 20px 15px;
}
div.tasks > img{
   width: 100%;
}
div.text > p.title-budget{
    display: flex;
    justify-content: space-between;
}

ol > li.location{
    list-style-position: inside;
    list-style-image: url("../images/location-icon.jpg");
}

ol > li.due-date{
    list-style-position: inside;
    list-style-image: url("../images/calendar-icon.png");
}

ol > li.time-of-day{
    list-style-position: inside;
    list-style-image: url("../images/clock-icon.png");
}

div.text > div.profile-offers{
    display: grid;
    grid-template-rows: repeat(1fr, 2);
    grid-template-columns: 20px, 50px;
}
div.profile-offers>div.profile{
    grid-area: 1 / 1 / 3 / 2;
}
div.profile-offers>div.poster-name{
    grid-area: 1 / 2 / 2 / 3;
}
div.profile-offers>div.offers{
    grid-area: 2 / 2 / 3 / 3;
}